.sidebar {
  z-index: 10;
  top: 15px;
  right: 10px;
}

.sidebar ul {
  margin: 0;
  padding: 0;
}

.sidebar li {
  list-style-type: none;
  width: 40px;
  height: 40px;
  display: inline-block;
  margin-right: 10px;
}
.sidebar li a {
  width: 100%;
  height: 100%;
  position: relative;
}
.sidebar li:not(:last-child) {
  padding-bottom: 7px;
}

.sidebar li a svg {
  height: 40px;
  width: 40px;
}

.sidebar li a svg path {
  fill: rgba(255, 255, 255, 0.7);
}

.sidebar li a:hover svg path {
  -webkit-transition: all ease 0.3s;
  transition: all ease 0.3s;
}

.sidebar li a:hover #linkedin path {
  fill: #0077b5;
}
.sidebar li a:hover #facebook path {
  fill: #4267b2;
}
.sidebar li a:hover #github path {
  fill: #24292d;
}
.sidebar li a:hover #mail path {
  fill: rgb(24, 24, 24);
}

.sidebar li a svg#mail {
  margin-left: 2px;
  height: 38px;
  width: 37px;
}

.item-bg {
  position: absolute;
  width: 34px;
  height: 34px;
  border-radius: 20px;
  position: absolute;
  left: 3px;
  top: -25px;
  z-index: -10;
}

a:hover .item-bg {
  background: white;
  -webkit-transition: all ease 0.3s;
  transition: all ease 0.3s;
}

.portfolio-item {
  position: relative;
  width: 31.33%;
  margin: 1%;
  padding-bottom: 20%;
  background: white center center no-repeat;
  background-size: cover;
  text-align: center;
  padding-left: 0 !important;
  cursor: pointer;
  border-radius: 5px;
}

.portfolio-item .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  text-align: center;
  background: rgba(0, 0, 0, 0.7);
  z-index: 100;
  box-sizing: border-box;
  opacity: 0;
  border-radius: 5px;
}

.portfolio-item .overlay h2 {
  position: absolute;
  top: 50%;
  margin-top: -12px;
  font-size: 24px;
  vertical-align: middle;
  text-transform: uppercase;
  color: white;
  text-align: center;
  width: 99%;
}

.portfolio-item:hover .overlay {
  opacity: 1;
  -webkit-transition: ease all 0.5s;
  transition: ease all 0.5s;
}

@media only screen and (max-width: 600px) {
  .portfolio-item {
    width: 98%;
    padding-bottom: 60%;
    margin: 10px 1%;
  }
}
@media only screen and (max-width: 900px) and (min-width: 600px) {
  .portfolio-item {
    width: 48%;
    padding-bottom: 30%;
    margin: 10px 1%;
  }
}

.portfolio-modal {
  display: none;
  /*display: block;*/
  background: rgba(0, 0, 0, 0.9);
  position: fixed;
  z-index: 1000;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.portfolio-modal .container {
  color: white;
  max-width: 700px;
  margin: auto;
  text-align: center;
  padding: 100px 0;
  font-family: Lato, 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

.portfolio-modal .container h2 {
  padding-bottom: 50px;
  font-size: 42px;
  -webkit-font-feature-settings: "c2sc";
          font-feature-settings: "c2sc";
  font-variant: small-caps;
  margin: 0;
  color: #283d86;
}

.portfolio-modal .container p {
  font-size: 18px;
  line-height: 28.5px;
}

.portfolio-modal .container ul li a {
  text-decoration: none;
  color: white;
  border: 2px solid white;
  border-radius: 40px;
  padding: 10px;
  font-size: 20px;
  line-height: 30px;
  cursor: pointer;
}
.portfolio-modal .container ul {
  margin: 50px auto;
  padding: 0px;
}
.portfolio-modal .container ul li {
  list-style-type: none;
  display: inline-block;
  padding: 0 5px;
}
.portfolio-modal .container ul li a:hover {
  background: white;
  -webkit-transition: all ease 0.5s;
  transition: all ease 0.5s;
  color: #17234d;
}
.portfolio-modal .container ul li a svg {
  position: absolute;
  width: 30px;
  height: 30px;
  fill: white;
}
.portfolio-modal .container ul li a span {
  margin-left: 35px;
}
.portfolio-modal .container ul li a:hover svg {
  fill: #17234d;
  -webkit-transition: all ease 0.5s;
  transition: all ease 0.5s;
}

.displayBlock {
  display: block;
}

.modal-close {
  position: absolute;
  top: 40px;
  right: 40px;
}
.modal-close svg {
  width: 70px;
  height: 70px;
  cursor: pointer;
}
.modal-close svg path {
  fill: white;
}
.modal-close svg:hover path {
  fill: rgba(255, 255, 255, 0.6);
  -webkit-transition: all ease 0.3s;
  transition: all ease 0.3s;
}

@media only screen and (max-width: 900px) {
  .modal-close svg {
    width: 50px;
    height: 50px;
  }
  .portfolio-modal .container {
    padding: 100px 15px;
  }
  .portfolio-modal .container h2 {
    padding-bottom: 40px;
    font-size: 36px;
  }

  .portfolio-modal .container p {
    font-size: 16px;
    line-height: 25.5px;
  }
  .portfolio-modal .container ul li a svg {
    position: absolute;
    width: 25px;
    height: 25px;
    fill: white;
  }
  .portfolio-modal .container ul li a span {
    margin-left: 30px;
  }
  .portfolio-modal .container ul li a {
    border-radius: 40px;
    padding: 10px;
    font-size: 16px;
    line-height: 25px;
    cursor: pointer;
  }
}
@media only screen and (max-width: 600px) {
  .modal-close svg {
    width: 30px;
    height: 30px;
  }
  .portfolio-modal .container {
    padding: 100px 10px;
  }
  .portfolio-modal .container h2 {
    padding-bottom: 30px;
    font-size: 32px;
  }
  .portfolio-modal .container p {
    font-size: 14px;
    line-height: 20px;
  }
}

html {
  scroll-behavior: smooth;
}

.particles {
  position: absolute;
  width: 100%;
  height: 100vh;
  background-image: -webkit-linear-gradient(
    bottom right,
    rgb(20, 24, 31) 20%,
    #17234d,
    #0c011a
  );
  background-image: linear-gradient(
    to top left,
    rgb(20, 24, 31) 20%,
    #17234d,
    #0c011a
  );
}

.header {
  width: 100%;
  height: 100vh;

  /*background: url('../img/forest-mountains.jpg') center center no-repeat;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;*/
  opacity: 0.95;
  -webkit-transition: all ease 0.5s;
  transition: all ease 0.5s;
  text-align: center;
}

.header-content {
  position: relative;
  color: white;
  text-align: center;
  width: 100%;
  font-family: 'Palatino';
  letter-spacing: 1.05px;
  top: 40%;
}

.header-content h1 {
  font-size: 64px;
  font-family: 'Palatino';
  padding: 0;
  margin: 0;
}

.header-content span {
  display: block;
  font-size: 22px;
  padding-bottom: 15px;
}

.down-button {
  position: absolute;
  bottom: 0px;
  -webkit-transition: all ease 0.5s;
  transition: all ease 0.5s;
}
.down-button svg {
  width: 70px;
  height: 70px;
  margin-left: -35px;
}
.down-button svg path {
  fill: rgba(255, 255, 255, 0.5);
}
.down-button:hover svg path {
  fill: rgba(255, 255, 255, 0.8);
  -webkit-transition: all ease 0.5s;
  transition: all ease 0.5s;
}

@media only screen and (max-width: 600px) {
  .header-content h1 {
    font-size: 48px;
  }
  .header-content span {
    display: block;
    font-size: 18px;
    padding-bottom: 15px;
  }
}

